  margin: 0 !important;
  display: flex;
}

.ad {
  margin: 0 !important;
  display: flex;
}

.pages {
  & /deep/ el-pagination__sizes {
    & /deep/ el-input__inner {
      height: 22px;
      line-height: 22px;
    }
  }
}


.el-button + .el-button {
  margin: 0;
}

.tables {
  & /deep/ .el-button--success {
    height: 35px;
    color: rgba(4, 4, 4, 1);
    border-width: 0px;
    border-style: solid;
    border-color: rgba(1, 1, 1, 1);
    border-radius: 4px;
    background-color: rgba(253, 228, 79, 1);
  }

  & /deep/ .el-button--primary {
    height: 35px;
    color: rgba(8, 8, 8, 1);
    border-width: 0px;
    border-style: solid;
    border-color: rgba(1, 1, 1, 1);
    border-radius: 4px;
    background-color: rgba(240, 125, 48, 1);
  }

  & /deep/ .el-button--danger {
    height: 35px;
    color: rgba(8, 8, 8, 1);
    border-width: 0px;
    border-style: solid;
    border-color: rgba(1, 1, 1, 1);
    border-radius: 4px;
    background-color: rgba(233, 95, 64, 1);
  }

  & /deep/ .el-button {
    margin: 4px;
